.navbar{
    box-sizing: border-box;
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 1px 1px 5px silver;
    width: 100%;
    padding: 1em;
    background-color: white;
    height: 4em;
    z-index: 5;
    cursor: pointer;
    font-weight: 500;
}
.navbar--left{
    display: flex;
    gap: 1em;
}
.navbar--left>*:hover{
    color: orangered;
}
.navbar--left--profile{
    display: flex;
    align-items: center;
    cursor: pointer;
}
.navbar--left--users{
    position: absolute;
    right: 0;
    top: 4em;
    padding: .5em;
    list-style: none;
    background-color: gainsboro;
    color: black;
    z-index: -5;
} 
.navbar--left--users li{
    padding:0.75em 1em;
}
.navbar--left--link{
    text-decoration: none;
}
.navbar--left--users li:hover{
    background-color: white;
    color: black;
}
.navbar--image{
    width: 1em;
    height: 1em;
    padding-inline:0.5em;
}