.mainpage{
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    align-items: center;
    gap: 0.5em;
}
.mainpage div{
    display: grid;
    grid-template-columns: 3em 15em;
    align-items: center;
}
.mainpage select{
    cursor: pointer;
    outline: none;
    border: none;
    padding: 1em 2em;
    background-color: whitesmoke;
}
.mainpage button{
    cursor: pointer;
    border: none;
    border-radius: 0.25em;
    background-color: orangered;
    color: white;
    padding: 0.5em 2em;
    margin: 0.5em 0em;
    font-size: larger;
}
@media (min-width:978px){
    .mainpage{
        display: flex;
        flex-direction: row;
    }
    .mainpage>div{
        display: flex;
        font-size: 0.75em;
        flex-direction: column;
        align-items: start;
        padding-inline: 1em;
        padding-block: 0.5em;
        height: 5em;
        background-color: whitesmoke;
        border-radius: 0.5em;
    }
    .mainpage select{
        width: 25em;
        background:whitesmoke;
    }
    .mainpage button{
        height: 3.5em;
        border-radius: .25em;
    }
}