.Buses{
    display: flex;
    justify-content: center;
    align-items: center;
}
.Bus{
    padding: 1em;
    padding-top: 5em;
    width: min(100%,50em);
}
.Bus-grid-head{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    padding:1em ;
    background-color: orangered;
    font-size: large;
    color: white;
    font-weight: 500;
}
.Bus--grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    align-items: center;
    box-shadow: 1px 1px 5px rgb(230, 230, 230);
    padding: 1em 0.5em;
    margin-block: 1.5em;
    border-radius: 0.25em;
}
.Bus--grid *{
    padding: 1em;
    margin-block: 0.25em;
}
.Bus--grid button{
    background-color: orangered;
    display: flex;
    border: none;
    border-radius: 0.5em;
    color: white;
    justify-content: center;
    align-items: center;
}
.Bus--grid button:hover{
    background-color: rgb(197, 53, 0);
}